.loading {
    padding: 1rem;
    font-size: 2rem;
    color: #fff;
}

.items {
    display: grid;
    gap: 1rem;
    justify-content: center;
    padding: 1rem;
    grid-template-columns: repeat(1, 1fr);
}

img {
    min-height: 100px;
    min-width: 200px;
    max-width: 500px;
    height: auto;
    width: 100%;
}

@media screen and (min-width: 428px) {
  .items {
    grid-template-columns: repeat(1, 1fr);
  }
}

@media screen and (min-width: 768px) {
  .items {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media screen and (min-width: 1000px) {
  .items {
    grid-template-columns: repeat(4, 1fr);
  }
}



.favorite-item {
    display: flex;
    justify-content: center;
    gap: 1rem;
}

.favorite {
    overflow-x: scroll;
    padding: 1rem;
}

h2 {
    color: #fff;
    margin: .5rem 0;
}

.search-favorites input[type=text] {
    padding: .75rem;
    margin-bottom: 12px;
}

.no-recipe{
    text-align: center;
    font-size: 24px;
    color: #fff;
}