.recipe-item {
    padding: 1rem .8rem;
    background-color: #eae9e9;
    border-radius: 16px;
}

.recipe-item img { 
    border-radius: 12px;
}

.recipe-item h3 {
    padding: .5rem 0;
}

.recipe-item .btn {
    padding: .5rem 1rem;
    cursor: pointer;
    border-radius: 4px;
    border: 1px solid #c2c2c2;
    transition: .3s;
}

.recipe-item .btn:hover {
   background-color: #fa6400;
   color: #fff;
}