.Search {
    display: flex;
    gap: 1rem;
    justify-content: center;

}

.Search input {
    padding: 10px 10px;
}


.Search button {
    background-color: #fa6400;
    padding: 10px 30px;
    color: #fff;
    font-size: 16px;
    box-shadow: 0 1px 3px 0 rgb(0 0 0 / 2%);
    border-radius: 4px;
    border-color: #fa6400;
    cursor: pointer;
}